<template>
  <div class="container">
	  <pullRefreshView @onRefresh="onRefresh">
		<headerbar></headerbar>
		<div class="top-img" :style="topImgStyle" @click="changeBg"></div>
		<div style="display:none;" id="uploaderBg">
			<input
				ref="uploaderBg"
				id="uploaderInput"
				class="weui-uploader__input"
				type="file"
				accept="image/*"
			/>
		</div>
		<div class="name-info">
			<p class="nickname"></p>
			<img  alt="avatar" :src="myAvatar" @click="gotoMyPage" class="avatar">
		</div>
		<list ref="list"></list>
		<div
			:style="{zIndex:showInput? '999': '-1', opacity:showInput? '1':'0'}"
			ref="inputBarWrap"
			class="input-wrap ios"
			v-if="iosInput">
			<inputBar ref="inputBar" :option="inputBarOption" @publish="publish"></inputBar>
		</div>
		<div
			:style= "{opacity:showInput ? '1': '0', bottom:showInput ? '0':'-60px'}"
			ref="inputBarWrap"
			class="input-wrap android"
			v-if="androidInput"
		>
		<inputBar ref="inputBar" :option="inputBarOption" @publish="publish"></inputBar>
		</div>
	  </pullRefreshView>
	  <div v-show="showPWA" @click="showPWA = false" class="add-screen"></div>
  </div>
</template>

<script>
import list from './list'
import headerbar from './headerbar'
import pullRefreshView from '@/components/pullRefreshView'
import inputBar from '@/components/inputBar'
import os from '@/util/os'
import service from '@/util/service'
export default {
  computed: {
    myAvatar () {
      return (
        this.$store.state.currentUser.avatar ||
				require('../../assets/missing_face.png')
      )
    },
    nickname () {
      return this.$store.state.currentUser.nickname || '游客账号'
    },
    topImgStyle () {
      let url = this.$store.state.currentUser.bgurl || require('../../assets/topbg.jpg')
      let obj = { backgroundImage: 'url(' + url + ')' }
      return obj
    }
  },
  components: {
    inputBar,
    list,
    headerbar,
    pullRefreshView
  },
  data () {
    return {
      inputBarOption: {
        noplus: true
      },
      showInput: false,
      showPWA: !(window.location.href.indexOf('manifest') > -1) && os.isIOS,
      iosInput: os.isIOS,
      androidInput: os.isAndroid
    }
  },
  mounted () {
    // 处理评论输入框的显示和隐藏
    this.$bus.$on('showInput', (flag, currentData) => {
      this.showInput = flag
      if (flag) {
		this.$refs.inputBar && this.$refs.inputBar.focusInput(currentData)
		 // 在ios里，页面会被顶上来，要单独处理
		if (os.isIOS) {
			// 设置input在手指点击的那个位置出现时表示稍微往下移动一些
			this.$refs.inputBarWrap.style.top = (currentData.pageY) + 'px'

			setTimeout(() => {
			// 输入框的位置减去键盘的高度减去位置微调系数 45 : 5
			let y = currentData.pageY - window.keywordHeight - (os.isIpP ? 45 : 5)
			window.scroll(0, y)
			}, 300)
		} 
      } else {
        try {
          this.$refs.inputBar.blurInput()
        } catch (e) {}
      }
    })
    var self = this
    weui.uploader('#uploaderBg', {
      url: service.baseURL + 'post/uploadimg',
      auto: true,
      type: 'file',
      fileVal: 'image',
      compress: {
        width: 1300,
        height: 1300,
        quality: 0.8
      },
      onBeforeQueued: function (files) {
        if (['image/jpg', 'image/jpeg', 'image/png', 'image/gif'].indexOf(this.type) < 0) {
          weui.alert('请上传图片')
          return false
        }
        if (this.size > 10 * 1024 * 1024) {
          weui.alert('请上传不要超过10M的照片')
          return false
        }
        if (files.length > 1) {
          weui.alert('最多只能上传一张照片')
          return false
        }
      },
      onBeforeSend: function (data, headers) {
        headers['wec-access-token'] = service.token
      },
      onSuccess: function (ret) {
        self.submitBg(ret)
      }
    })
  },
  methods: {
    onRefresh () {
      this.$refs.list.refresh()
    },
    // 修改背景图片
    changeBg () {
      weui.actionSheet([
        {
          label: '更换图片',
          onClick: () => {
            this.$refs.uploaderBg.click()
          }
        }
      ], [
        {
          label: '取消',
          onClick: function () {
            console.log("取消")
          }
        }
      ])
    },
    pxtovw (px) {
      return (px / 375 * 1000) + 'vw'
    },
    async publish (data) {
      // 存入数据库
      let resp = await service.post('likecomment/addcomment', {
        content: data.value,
        postId: data.data.id
      })
      if (resp.code === 0) {
        this.$store.dispatch('addComment', {
          content: data.value,
          pid: resp.data.post,
          user: this.$store.state.currentUser
        })
        this.showInput = false
      } else {
        weui.alert('评论失败')
      }
    },
    async submitBg (obj) {
      let resp = await service.post('users/update', {
        userId: this.$store.state.currentUser._id,
        bgurl: obj.data.url
      })
      if (resp.code === 0) {
        this.$store.dispatch('setUser', {
          ...this.$store.state.currentUser,
          bgurl: obj.data.url
        })
        weui.toast('修改成功', 3000)
      }
    },
    gotoMyPage () {
	  // 判断是否登录
      if (!this.$store.state.currentUser._id) {
        this.$router.push({
          path: 'login',
          name: 'login'
        })
        return
      }
      // 打开我的页面
      this.$router.push({
        path: 'mypage',
        name: 'mypage',
        params: {}
      })
    }
  }
}
</script>

<style scoped>
.top-img {
	height: 320px;
	width: 100%;
	background-size: cover;
	background-position: center center;
}
.avatar{
	width: 66px;
	height: 66px;
	border-radius: 4px;
}
.name-info {
	position: absolute;
	right: 12px;
	top: 273px;
	border-radius: 5px;
	display: flex;
}
.nickname {
	margin-right: 24px;
	margin-top: 17px;
	color: #fff;
	text-shadow: 1px 1px 2px #000000;
	font-size: 16px;
}
.input-wrap.ios{
	position: absolute;
	left: 0;
	right: 0;
	top: 80px;
	z-index: -1;
}
.input-wrap.android {
	position: fixed;
	left: 0;
	bottom: -60px;
	right: 0;
	z-index: 999;
}
.add-screen {
  position: fixed;
  bottom: 15px;
  left:50%;
  width: 300px;
  height: 166px;
  background-image: url('//wecircle.oss-cn-beijing.aliyuncs.com/image-1560843180441.PNG?x-oss-process=image/resize,l_300');
  background-size: cover;
  transform: translateX(-50%);
  z-index: 999;
  border: 1px solid #ccc;
}
</style>
