import Vue from 'vue'
import Router from 'vue-router'
import publish from './views/publish'
import wecircle from './views/wecircle'
import mypage from './views/mypage'
import personpage from './views/personpage'
Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'wecircle',
      component: wecircle
    },
    {
      path: '/login',
      name: 'login',
      component: () => import('./components/login/index.vue')
    },
    {
      path: '/publish',
      name: 'publish',
      component: publish
      // component: () => import(/* webpackChunkName: "publish" */ './views/publish')
	},
	{
		path:'/mypage',
		name:'mypage',
		component: mypage
	},
	{
		path:'/personpage',
		name:'personpage',
		component: personpage
	},
	{
		path:'/changenickname',
		name:'changenickname',
		component: () => import('./views/changenickname')
	},
	{
		path:'/changedesc',
		name:'changedesc',
		component:() => import('./views/changedesc')
	},
	{
		path:'/chat',
		name:'chat',
		component: () => import('./views/chat')
	},
	{
		path:'/chatlist',
		name:'chatlist',
		component: () => import('./views/chatlist')
	}
  ],
  scrollBehavior (to, from, savedPosition) {
	if (savedPosition) {
		return savedPosition
	} else {
		return {x: 0, y: 0}
	}
  }
})
