<template>
  <div class="list-content" @touchstart="touchstart($event)">
	  <scrollView
	  	@loadCallback = "loadCallback"
		:isend = "isend"
		:readyToLoad = "readyToLoad"
		@scroll = "scroll"
	  >
	  <ul>
		  <li v-for="(item,index) in dataList" :key="index">
			  <postItem :data="item"></postItem>
		  </li>
	  </ul>
	  </scrollView>
  </div>
</template>

<script>
import postItem from '@/components/postItem'
import service from '@/util/service'
import scrollView from '@/components/scrollView'
import formatTime from '@/util/formatTime'

export default {
  name: 'list',
  components: {
    postItem,
    scrollView
  },
  data () {
    return {
      isend: false,
      readyToLoad: true,
      pageStart: 0,
      scrollTop: 0,
      lessDataFlag: false
    }
  },
  computed: {
    dataList () {
      return this.$store.state.wecircleDataList
    }
  },
  async created () {
    // 第一次进入,要发起请求
    if (!this.dataList.length) {
      this.refresh()
    }
    // 刚发表完成，立刻刷新下列表
    if (this.$router.justPub) {
      this.refresh()
      this.$router.justPub = false
    }
  },
  methods: {
    scroll (top) {
      if (top <= 100) {
        this.$bus.$emit('hideHeader')
      } else {
        this.$bus.$emit('showHeader')
      }
    },
    refresh () {
      this.pageStart = 0
      this.$store.dispatch('setWecircleDataList', { first: true })
      this.fetchData()
    },
    touchstart (evt) {
      if (evt.target.classList.contains('opera-box')) {
        return
      }
      if (evt.target.classList.contains('common-icon') || evt.target.classList.contains('common-text')) {
        return
      }
      if (evt.target.classList.contains('like-icon') || evt.target.classList.contains('like-text')) {
        return
      }
      // 在开始滚动时 要把 评论输入框和点赞评论面板 隐藏
      this.$store.dispatch('closeCLPanel', true)
      this.$bus.$emit('showInput', false, this.data)
    },
    async fetchData () {
      // 是否可以发起下一次滚动加载请求的标志位
      this.readyToLoad = false
      // 拉取数据
      let resp = await service.get('post/getcirclepost', {
        pageStart: this.pageStart
      })
      // 对数据做一下处理
      this.formatData(resp.data)
    },
    formatData (result) {
      let array = []
      result.forEach(item => {
        array.push({
          id: item._id,
          avatar: item.user.avatar,
          nickname: item.user.nickname,
          content: item.content,
          piclist: item.piclist,
          comments: item.comments,
          time: formatTime(new Date(item.create).getTime() / 1000),
		  isLike: item.isLike,
		  likes: item.likes,
          user: item.user
        })
	  })
      // 通过vuex改变列表数据
	  this.$store.dispatch('setWecircleDataList', array)
      // 如果返回的数据未空，证明没有数据了，就把停止滚动加载的标志位设置为true
      if (result.length === 0) {
        this.isend = true
      }
      // 如果第一页并且第一屏数据太少，就自动再发一次请求，避免内容高度不够，无法触滚动加载
      if (this.pageStart === 0 && result.length < 4 && !this.lessDataFlag) {
        this.loadCallback()
        this.lessDataFlag = true
      }
      // 重置标志位
      this.readyToLoad = true
    },
    loadCallback () {
      this.pageStart++
      this.fetchData()
    }
  }
}
</script>

<style scoped>
.list-content {
  background-color: #fff;
}
</style>
