import * as OfflinePluginRuntime from 'offline-plugin/runtime'
import service from '@/util/service'

OfflinePluginRuntime.install({
	onUpdateReady: () => {
		// 更新完成之后,调用applyUpdate即skipwaiting()方法
		OfflinePluginRuntime.applyUpdate()
	},
	onUpdated: () => {
		// 弹一个确认框
		weui.confirm('发现新版本，是否更新?', () => {
			// 刷新一下页面
			window.location.reload()
		},() => {

		},{
			title: ''
		})
	}
})

if (!navigator.serviceWorker) {
	weui.topTips('请使用指定浏览器,体验前沿的技术')
}
navigator.serviceWorker && navigator.serviceWorker.ready.then((registration) => {
	const publicKey = 'BHSpcTZY7MvELY7OD8pLrNM_a90auIL33JaP1ikq-Rzjl3Q_tVh1OJzdHAsNftl2RVRKlMpS1ovwXr6Kn9eH2cc'
	try {
		if (window.PushManager) {
			registration.pushManager.getSubscription().then(subscription => {
				if(!subscription && window.localStorage.getItem('cuser')) {
					const subscription = registration.pushManager.subscribe({
						userVisibleOnly: true,
						applicationServerKey: urlBase64ToUnit8Array(publicKey)
					}).then(function(subscription) {
						weui.topTips('获取到subscription')
						if (subscription && subscription.endpoint) {
							let resp = service.post('users/addsubscription',{
								subscription: JSON.stringify(subscription)
							})
						}
					}).catch(function(err){
						weui.topTips(err)
					})
				} else {
					weui.topTips('已经订阅过')
				}
			})
		}
	} catch (e) {
		console.log(e)
	}
})
function urlBase64ToUnit8Array (base64String) {
	const padding = '='.repeat((4 - base64String.length % 4) % 4)
	const base64 = (base64String + padding)
					.replace(/\-/g, '+')
					.replace(/_/g, '/')
	const rawData = window.atob(base64)
	const outputArray = new Uint8Array(rawData.length)
	
	for(let i = 0; i < rawData.length; ++i) {
		outputArray[i] = rawData.charCodeAt(i)
	}
	return outputArray
}