<template>
   <div class="container">
		<div class="header">
			<div class="cancel" @click="cancel">取消</div>
			<div class="create weui-btn weui-btn_mini weui-btn_primary" @click="publish">发表</div>
		</div>
		<div class="input-content">
			<textarea
				class="weui-textarea"
				@input="oninput"
				placeholder="这一刻的想法.."
				v-model="content"
				maxlength="70"
			></textarea>
			<div class="weui-textarea-counter">
				<span>{{this.textCount}}</span>/70
			</div>
		</div>
		 <ul class="img-content">
			<div id="uploaderPub">
				<div class="weui-uploader">
					<div class="weui-uploader__bd">
						<ul class="weui-uploader__files" id="uploaderFiles" @click="showImg($event)"></ul>
						<div class="weui-uploader__input-box">
						<input
							id="uploaderInputPub"
							class="weui-uploader__input"
							type="file"
							accept="image/*"
							multiple="multiple"
						>
						</div>
					</div>
					<div class="weui-uploader__hd">
						<div class="weui-uploader__info">
						<span id="uploadCount">{{uploadCount}}</span>/5
						</div>
					</div>
				</div>
			</div>
		</ul>
   </div>
</template>
<script>
import service from '@/util/service'
import { getCookie } from '@/util/cookie'
export default {
  name: 'publish',
  data () {
    return {
      uploadCount: 0,
      content: '',
      textCount: 0,
      picList: []
    }
  },
  mounted () {
    let self = this
    weui.uploader('#uploaderPub', {
      url: service.baseURL + '/post/uploadimgaliyun', // 上传服务的后台接口，返回值需要使用json格式
      auto: true,
      type: 'file',
      fileVal: 'image',
      compress: {
        width: 1300,
        height: 1300,
        quality: 0.8
      },
      onBeforeQueued: function (files) {
        if (['image/jpg', 'image/jpeg', 'image/png', 'image/gif'].indexOf(this.type) < 0) {
          weui.alert('请上传图片')
          return false
        }
        if (this.size > 10 * 1024 * 1024) {
          weui.alert('请上传不超过10M的图片')
          return false
        }
        if (files.length > 5) {
          weui.alert('最多只能上传5张图片,请重新选择')
          return false
        }
        if (self.uploadCount + 1 > 5) {
          weui.alert('最多只能上传5张图片')
          return false
        }
        ++self.uploadCount
      },
      onBeforeSend: function (data, headers) {
        // console.log(this, data, headers);
        // 将token字段放在headers里，API校验
        headers['wec-access-token'] = getCookie('token')
        // return false; // 阻止文件上传
      },
      onProgress: function (procent) {
        // console.log(this, procent);
        // return true; // 阻止默认行为，不使用默认的进度显示
      },
      onSuccess: function (ret) {
        // console.log(this, ret)
        ret.data.id = this.id // ret.data是后台上传接口的返回json的数据
        // 将选择的图片放在一个self.picList数组里
        self.picList.push(ret.data)
        // return true; // 阻止默认行为，不使用默认的成功态
      },
      onError: function () {
      }
    })
  },
  methods: {
    oninput () {
      this.textCount = this.content.length
    },
    /*
		 * 点击已经上传的图片
		 */
    showImg (e) {
      let target = e.target
      if (target.classList.contains('weui-uploader__file')) {
        // 找到点击的那个元素
        let url = target
						  .getAttribute('style')
						  .match(/url\((.*?)\)/)[1]
						  .replace(/"/g, '')
        let gallery = weui.gallery(url, {
          onDelete: () => {
            this.deleteImg(target, gallery)
          }
        })
      }
    },
    cancel () {
      this.$router.go(-1)
    },
    // 删除已上传的图片
    deleteImg (target, gallery) {
      let id = target.getAttribute('data-id')
      if (confirm('确定删除该图片?')) {

      }
      for (var i = 0, len = this.picList.length; i < len; ++i) {
        var file = this.picList[i]
        if (file.id == id) {
          this.picList.splice(i - 1, 1)
          break
        }
      }
      target.remove()
      gallery.hide()
      this.uploadCount--
    },
    async publish () {
      if (!this.content) {
        return
      }
      let resp = await service.post('post/savepost', {
        content: this.content,
        picList: this.picList
      })
      if (resp.code === 0) {
        this.$router.go(-1)
        this.$router.justPub = true
      }
    }

  }

}
</script>

